import { useState, useCallback } from 'react'
import { Plus, Trash2, Clock } from 'lucide-react'
import { Button } from '@/components/ui/button'

// =============================================================================
// Types
// =============================================================================

type SubtaskStatus = 'TODO' | 'IN_PROGRESS' | 'DONE'

interface Subtask {
  id: number
  title: string
  status: SubtaskStatus
  position: number
  timeEstimated: number
  timeSpent: number
  user?: {
    id: number
    username: string
    name: string | null
  } | null
}

export interface SubtaskListProps {
  taskId: number
  subtasks: Subtask[]
  isLoading: boolean
  onCreate: (data: { taskId: number; title: string }) => Promise<unknown>
  onUpdate: (data: { subtaskId: number; status?: SubtaskStatus; title?: string }) => Promise<unknown>
  onDelete: (data: { subtaskId: number }) => Promise<unknown>
  isCreating: boolean
}

// =============================================================================
// Constants
// =============================================================================

const STATUS_CYCLE: Record<SubtaskStatus, SubtaskStatus> = {
  TODO: 'IN_PROGRESS',
  IN_PROGRESS: 'DONE',
  DONE: 'TODO',
}

// =============================================================================
// Helper Functions
// =============================================================================

function formatTime(hours: number): string {
  if (hours === 0) return ''
  if (hours < 1) {
    const mins = Math.round(hours * 60)
    return `${mins}m`
  }
  const h = Math.floor(hours)
  const m = Math.round((hours - h) * 60)
  return m > 0 ? `${h}h ${m}m` : `${h}h`
}

// =============================================================================
// SubtaskItem Component
// =============================================================================

function SubtaskItem({
  subtask,
  onUpdate,
  onDelete,
}: {
  subtask: Subtask
  onUpdate: (data: { subtaskId: number; status?: SubtaskStatus; title?: string }) => Promise<unknown>
  onDelete: (data: { subtaskId: number }) => Promise<unknown>
}) {
  const [isEditing, setIsEditing] = useState(false)
  const [editedTitle, setEditedTitle] = useState(subtask.title)

  const handleStatusToggle = useCallback(async () => {
    // Cycle through statuses: TODO -> IN_PROGRESS -> DONE -> TODO
    const nextStatus = STATUS_CYCLE[subtask.status]
    await onUpdate({ subtaskId: subtask.id, status: nextStatus })
  }, [subtask.id, subtask.status, onUpdate])

  const handleTitleSave = useCallback(async () => {
    if (editedTitle.trim() && editedTitle !== subtask.title) {
      await onUpdate({ subtaskId: subtask.id, title: editedTitle.trim() })
    }
    setIsEditing(false)
  }, [editedTitle, subtask.id, subtask.title, onUpdate])

  const handleKeyDown = useCallback(
    (e: React.KeyboardEvent) => {
      if (e.key === 'Enter') {
        handleTitleSave()
      } else if (e.key === 'Escape') {
        setEditedTitle(subtask.title)
        setIsEditing(false)
      }
    },
    [handleTitleSave, subtask.title]
  )

  const handleDelete = useCallback(async () => {
    if (confirm('Delete this subtask?')) {
      await onDelete({ subtaskId: subtask.id })
    }
  }, [subtask.id, onDelete])

  const isDone = subtask.status === 'DONE'
  const isInProgress = subtask.status === 'IN_PROGRESS'

  return (
    <div className="group flex items-center gap-3 p-2 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-800">
      {/* Checkbox */}
      <button
        onClick={handleStatusToggle}
        className={`w-5 h-5 rounded border-2 flex items-center justify-center transition-colors ${
          isDone
            ? 'bg-green-500 border-green-500 text-white'
            : isInProgress
              ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
              : 'border-gray-300 dark:border-gray-600 hover:border-gray-400'
        }`}
      >
        {isDone && (
          <svg className="w-3 h-3" viewBox="0 0 12 12" fill="none">
            <path
              d="M2 6L5 9L10 3"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
          </svg>
        )}
        {isInProgress && (
          <div className="w-2 h-2 rounded-full bg-blue-500" />
        )}
      </button>

      {/* Title */}
      <div className="flex-1 min-w-0">
        {isEditing ? (
          <input
            type="text"
            value={editedTitle}
            onChange={(e) => setEditedTitle(e.target.value)}
            onBlur={handleTitleSave}
            onKeyDown={handleKeyDown}
            className="w-full bg-transparent border-b border-blue-500 focus:outline-none text-sm"
            autoFocus
          />
        ) : (
          <span
            className={`text-sm cursor-pointer ${
              isDone ? 'text-gray-400 line-through' : 'text-gray-900 dark:text-white'
            }`}
            onClick={() => setIsEditing(true)}
          >
            {subtask.title}
          </span>
        )}
      </div>

      {/* Status Badge */}
      {isInProgress && (
        <span className="text-xs font-medium text-blue-600 dark:text-blue-400 bg-blue-100 dark:bg-blue-900/30 px-1.5 py-0.5 rounded">
          In Progress
        </span>
      )}

      {/* Time */}
      {(subtask.timeEstimated > 0 || subtask.timeSpent > 0) && (
        <div className="flex items-center gap-1 text-xs text-gray-500 dark:text-gray-400">
          <Clock className="w-3 h-3" />
          {subtask.timeSpent > 0 && (
            <span>{formatTime(subtask.timeSpent)}</span>
          )}
          {subtask.timeSpent > 0 && subtask.timeEstimated > 0 && <span>/</span>}
          {subtask.timeEstimated > 0 && (
            <span className="text-gray-400">{formatTime(subtask.timeEstimated)}</span>
          )}
        </div>
      )}

      {/* Assignee */}
      {subtask.user && (
        <span className="text-xs text-gray-500 dark:text-gray-400">
          {subtask.user.name ?? subtask.user.username}
        </span>
      )}

      {/* Delete Button */}
      <button
        onClick={handleDelete}
        className="opacity-0 group-hover:opacity-100 p-1 text-gray-400 hover:text-red-500 transition-opacity"
      >
        <Trash2 className="w-4 h-4" />
      </button>
    </div>
  )
}

// =============================================================================
// AddSubtaskForm Component
// =============================================================================

function AddSubtaskForm({
  taskId,
  onCreate,
  isCreating,
}: {
  taskId: number
  onCreate: (data: { taskId: number; title: string }) => Promise<unknown>
  isCreating: boolean
}) {
  const [title, setTitle] = useState('')
  const [isAdding, setIsAdding] = useState(false)

  const handleSubmit = useCallback(async () => {
    if (title.trim()) {
      await onCreate({ taskId, title: title.trim() })
      setTitle('')
    }
  }, [taskId, title, onCreate])

  const handleKeyDown = useCallback(
    (e: React.KeyboardEvent) => {
      if (e.key === 'Enter') {
        handleSubmit()
      } else if (e.key === 'Escape') {
        setTitle('')
        setIsAdding(false)
      }
    },
    [handleSubmit]
  )

  if (!isAdding) {
    return (
      <button
        onClick={() => setIsAdding(true)}
        className="flex items-center gap-2 w-full p-2 text-sm text-gray-500 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-800 rounded-lg transition-colors"
      >
        <Plus className="w-4 h-4" />
        Add subtask
      </button>
    )
  }

  return (
    <div className="flex items-center gap-2 p-2">
      <div className="w-5 h-5 rounded border-2 border-gray-300 dark:border-gray-600" />
      <input
        type="text"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        onBlur={() => {
          if (!title.trim()) setIsAdding(false)
        }}
        onKeyDown={handleKeyDown}
        placeholder="Subtask title..."
        className="flex-1 bg-transparent border-b border-gray-300 dark:border-gray-600 focus:border-blue-500 focus:outline-none text-sm"
        autoFocus
        disabled={isCreating}
      />
      <Button
        size="sm"
        onClick={handleSubmit}
        disabled={isCreating || !title.trim()}
      >
        Add
      </Button>
    </div>
  )
}

// =============================================================================
// SubtaskList Component
// =============================================================================

export function SubtaskList({
  taskId,
  subtasks,
  isLoading,
  onCreate,
  onUpdate,
  onDelete,
  isCreating,
}: SubtaskListProps) {
  // Calculate progress
  const completedCount = subtasks.filter((s) => s.status === 'DONE').length
  const progressPercent = subtasks.length > 0
    ? Math.round((completedCount / subtasks.length) * 100)
    : 0

  if (isLoading) {
    return (
      <div className="space-y-2 animate-pulse">
        {[1, 2, 3].map((i) => (
          <div key={i} className="h-10 bg-gray-200 dark:bg-gray-700 rounded" />
        ))}
      </div>
    )
  }

  return (
    <div className="space-y-4">
      {/* Progress */}
      {subtasks.length > 0 && (
        <div className="space-y-1">
          <div className="flex items-center justify-between text-sm">
            <span className="text-gray-600 dark:text-gray-400">Progress</span>
            <span className="font-medium">
              {completedCount}/{subtasks.length} ({progressPercent}%)
            </span>
          </div>
          <div className="h-2 bg-gray-200 dark:bg-gray-700 rounded-full overflow-hidden">
            <div
              className="h-full bg-green-500 rounded-full transition-all"
              style={{ width: `${progressPercent}%` }}
            />
          </div>
        </div>
      )}

      {/* Subtask List */}
      <div className="space-y-1">
        {subtasks
          .sort((a, b) => a.position - b.position)
          .map((subtask) => (
            <SubtaskItem
              key={subtask.id}
              subtask={subtask}
              onUpdate={onUpdate}
              onDelete={onDelete}
            />
          ))}
      </div>

      {/* Add Form */}
      <AddSubtaskForm
        taskId={taskId}
        onCreate={onCreate}
        isCreating={isCreating}
      />
    </div>
  )
}

export default SubtaskList
