/*
 * EditProfile Page
 * Version: 1.0.0
 *
 * User profile editing page with form for basic info, theme, and preferences.
 *
 * Task: USER-01 (Task 247)
 */

import { useState, useEffect } from 'react'
import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardDescription,
} from '../../components/ui/card'
import { Button } from '../../components/ui/button'
import { Input } from '../../components/ui/input'
import { Label } from '../../components/ui/label'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Component
// =============================================================================

export function EditProfile() {
  const [name, setName] = useState('')
  const [timezone, setTimezone] = useState('')
  const [language, setLanguage] = useState('')
  const [theme, setTheme] = useState<'light' | 'dark' | 'system'>('system')
  const [hasChanges, setHasChanges] = useState(false)

  const utils = trpc.useUtils()
  const { data: profile, isLoading } = trpc.user.getProfile.useQuery()

  const updateProfile = trpc.user.updateProfile.useMutation({
    onSuccess: () => {
      utils.user.getProfile.invalidate()
      setHasChanges(false)
    },
  })

  // Initialize form with profile data
  useEffect(() => {
    if (profile) {
      setName(profile.name ?? '')
      setTimezone(profile.timezone ?? '')
      setLanguage(profile.language ?? '')
      setTheme((profile.theme as 'light' | 'dark' | 'system') ?? 'system')
    }
  }, [profile])

  // Track changes
  useEffect(() => {
    if (profile) {
      const changed =
        name !== (profile.name ?? '') ||
        timezone !== (profile.timezone ?? '') ||
        language !== (profile.language ?? '') ||
        theme !== ((profile.theme as 'light' | 'dark' | 'system') ?? 'system')
      setHasChanges(changed)
    }
  }, [name, timezone, language, theme, profile])

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    updateProfile.mutate({
      name: name || undefined,
      timezone: timezone || undefined,
      language: language || undefined,
      theme,
    })
  }

  if (isLoading) {
    return (
      <ProfileLayout title="Edit Profile" description="Update your profile information">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading profile...</p>
        </div>
      </ProfileLayout>
    )
  }

  if (!profile) {
    return (
      <ProfileLayout title="Edit Profile" description="Update your profile information">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Could not load profile</p>
        </div>
      </ProfileLayout>
    )
  }

  return (
    <ProfileLayout title="Edit Profile" description="Update your profile information">
      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Basic Information */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Basic Information</CardTitle>
            <CardDescription>
              Your display name and personal details
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <Input
                id="email"
                type="email"
                value={profile.email}
                disabled
                className="bg-gray-50 dark:bg-gray-800"
              />
              <p className="text-xs text-muted-foreground">
                Email cannot be changed here
              </p>
            </div>

            <div className="space-y-2">
              <Label htmlFor="username">Username</Label>
              <Input
                id="username"
                value={profile.username}
                disabled
                className="bg-gray-50 dark:bg-gray-800"
              />
              <p className="text-xs text-muted-foreground">
                Username cannot be changed
              </p>
            </div>

            <div className="space-y-2">
              <Label htmlFor="name">Display Name</Label>
              <Input
                id="name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="Your name"
              />
            </div>
          </CardContent>
        </Card>

        {/* Preferences */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Preferences</CardTitle>
            <CardDescription>
              Customize your experience
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="theme">Theme</Label>
              <select
                id="theme"
                value={theme}
                onChange={(e) => setTheme(e.target.value as 'light' | 'dark' | 'system')}
                className="w-full h-10 px-3 rounded-md border border-input bg-background text-sm"
              >
                <option value="system">System Default</option>
                <option value="light">Light</option>
                <option value="dark">Dark</option>
              </select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="timezone">Timezone</Label>
              <Input
                id="timezone"
                value={timezone}
                onChange={(e) => setTimezone(e.target.value)}
                placeholder="e.g., Europe/Amsterdam"
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="language">Language</Label>
              <Input
                id="language"
                value={language}
                onChange={(e) => setLanguage(e.target.value)}
                placeholder="e.g., en, nl"
              />
            </div>
          </CardContent>
        </Card>

        {/* Submit */}
        <div className="flex justify-end gap-3">
          <Button
            type="button"
            variant="outline"
            onClick={() => {
              if (profile) {
                setName(profile.name ?? '')
                setTimezone(profile.timezone ?? '')
                setLanguage(profile.language ?? '')
                setTheme((profile.theme as 'light' | 'dark' | 'system') ?? 'system')
              }
            }}
            disabled={!hasChanges}
          >
            Reset
          </Button>
          <Button
            type="submit"
            disabled={!hasChanges || updateProfile.isPending}
          >
            {updateProfile.isPending ? 'Saving...' : 'Save Changes'}
          </Button>
        </div>

        {updateProfile.isSuccess && (
          <div className="text-sm text-green-600 dark:text-green-400 text-center">
            Profile updated successfully!
          </div>
        )}

        {updateProfile.isError && (
          <div className="text-sm text-red-600 dark:text-red-400 text-center">
            Failed to update profile. Please try again.
          </div>
        )}
      </form>
    </ProfileLayout>
  )
}

export default EditProfile
