import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom'
import { HomePage } from './pages/Home'
import { LoginPage } from './pages/Login'
import { RegisterPage } from './pages/Register'
import { WorkspaceSettingsPage } from './pages/WorkspaceSettings'
import { ProjectListPage } from './pages/ProjectList'
import { BoardViewPage } from './pages/BoardView'
import { ListViewPage } from './pages/ListView'
import { CalendarViewPage } from './pages/CalendarView'
import { TimelineViewPage } from './pages/TimelineView'
import { MilestoneViewPage } from './pages/MilestoneView'
import { AnalyticsDashboard } from './pages/AnalyticsDashboard'
import { ImportExportPage } from './pages/ImportExport'
import { ApiSettings } from './pages/ApiSettings'
import { WebhookSettings } from './pages/WebhookSettings'
import { BoardSettingsPage } from './pages/BoardSettings'
// Profile pages
import { ProfileSummary } from './pages/profile/ProfileSummary'
import { TimeTracking } from './pages/profile/TimeTracking'
import { LastLogins } from './pages/profile/LastLogins'
import { Sessions } from './pages/profile/Sessions'
import { PasswordHistory } from './pages/profile/PasswordHistory'
import { Metadata } from './pages/profile/Metadata'
import { EditProfile } from './pages/profile/EditProfile'
import { Avatar } from './pages/profile/Avatar'
import { TwoFactorAuth } from './pages/profile/TwoFactorAuth'
import { PublicAccess } from './pages/profile/PublicAccess'
import { NotificationPreferences } from './pages/profile/NotificationPreferences'
import { ExternalAccounts } from './pages/profile/ExternalAccounts'
import { Integrations } from './pages/profile/Integrations'
import { ApiTokens } from './pages/profile/ApiTokens'
import { HourlyRate } from './pages/profile/HourlyRate'
import { ChangePassword } from './pages/profile/ChangePassword'
import { ProtectedRoute } from './components/auth/ProtectedRoute'
import { useAppSelector } from './store'
import { selectIsAuthenticated } from './store/authSlice'

/**
 * Auth-aware redirect component
 * Redirects authenticated users away from auth pages
 */
function AuthRedirect({ children }: { children: React.ReactNode }) {
  const isAuthenticated = useAppSelector(selectIsAuthenticated)
  if (isAuthenticated) {
    return <Navigate to="/" replace />
  }
  return <>{children}</>
}

function App() {
  return (
    <BrowserRouter>
      <Routes>
        {/* Public routes */}
        <Route
          path="/login"
          element={
            <AuthRedirect>
              <LoginPage />
            </AuthRedirect>
          }
        />
        <Route
          path="/register"
          element={
            <AuthRedirect>
              <RegisterPage />
            </AuthRedirect>
          }
        />

        {/* Protected routes */}
        <Route
          path="/"
          element={
            <ProtectedRoute>
              <HomePage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/projects"
          element={
            <ProtectedRoute>
              <ProjectListPage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/workspace/settings"
          element={
            <ProtectedRoute>
              <WorkspaceSettingsPage />
            </ProtectedRoute>
          }
        />
        {/* Profile routes */}
        <Route
          path="/profile"
          element={
            <ProtectedRoute>
              <ProfileSummary />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/timetracking"
          element={
            <ProtectedRoute>
              <TimeTracking />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/logins"
          element={
            <ProtectedRoute>
              <LastLogins />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/sessions"
          element={
            <ProtectedRoute>
              <Sessions />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/password-history"
          element={
            <ProtectedRoute>
              <PasswordHistory />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/metadata"
          element={
            <ProtectedRoute>
              <Metadata />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/edit"
          element={
            <ProtectedRoute>
              <EditProfile />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/avatar"
          element={
            <ProtectedRoute>
              <Avatar />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/2fa"
          element={
            <ProtectedRoute>
              <TwoFactorAuth />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/public"
          element={
            <ProtectedRoute>
              <PublicAccess />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/notifications"
          element={
            <ProtectedRoute>
              <NotificationPreferences />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/external"
          element={
            <ProtectedRoute>
              <ExternalAccounts />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/integrations"
          element={
            <ProtectedRoute>
              <Integrations />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/api"
          element={
            <ProtectedRoute>
              <ApiTokens />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/hourly-rate"
          element={
            <ProtectedRoute>
              <HourlyRate />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile/password"
          element={
            <ProtectedRoute>
              <ChangePassword />
            </ProtectedRoute>
          }
        />

        {/* Project routes */}
        <Route
          path="/project/:projectId/board"
          element={
            <ProtectedRoute>
              <BoardViewPage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/project/:projectId/list"
          element={
            <ProtectedRoute>
              <ListViewPage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/project/:projectId/calendar"
          element={
            <ProtectedRoute>
              <CalendarViewPage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/project/:projectId/timeline"
          element={
            <ProtectedRoute>
              <TimelineViewPage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/project/:projectId/milestones"
          element={
            <ProtectedRoute>
              <MilestoneViewPage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/project/:projectId/analytics"
          element={
            <ProtectedRoute>
              <AnalyticsDashboard />
            </ProtectedRoute>
          }
        />
        <Route
          path="/project/:projectId/import-export"
          element={
            <ProtectedRoute>
              <ImportExportPage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/project/:projectId/webhooks"
          element={
            <ProtectedRoute>
              <WebhookSettings />
            </ProtectedRoute>
          }
        />
        <Route
          path="/settings/api"
          element={
            <ProtectedRoute>
              <ApiSettings />
            </ProtectedRoute>
          }
        />
        <Route
          path="/project/:projectId/settings"
          element={
            <ProtectedRoute>
              <BoardSettingsPage />
            </ProtectedRoute>
          }
        />

        {/* Catch-all redirect */}
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
    </BrowserRouter>
  )
}

export default App
