import { type ReactNode, useState } from 'react'
import { useParams, useNavigate, Link } from 'react-router-dom'
import { WorkspaceSelector } from '@/components/workspace'
import { CommandPalette, useCommandPalette } from '@/components/command'
import { ShortcutsModal } from '@/components/common'
import { ProjectSidebar } from './ProjectSidebar'
import { useKeyboardShortcuts } from '@/hooks/useKeyboardShortcuts'
import { trpc } from '@/lib/trpc'

// =============================================================================
// Icons
// =============================================================================

function BackIcon() {
  return (
    <svg className="h-5 w-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
    </svg>
  )
}

function MenuIcon() {
  return (
    <svg className="h-5 w-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
    </svg>
  )
}

function CloseIcon() {
  return (
    <svg className="h-5 w-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
    </svg>
  )
}

// =============================================================================
// Types
// =============================================================================

export interface ProjectLayoutProps {
  children: ReactNode
}

// =============================================================================
// Component
// =============================================================================

export function ProjectLayout({ children }: ProjectLayoutProps) {
  const { projectId } = useParams<{ projectId: string }>()
  const navigate = useNavigate()
  const projectIdNum = projectId ? parseInt(projectId, 10) : null

  const [shortcutsOpen, setShortcutsOpen] = useState(false)
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false)
  const { open: openCommandPalette } = useCommandPalette()

  // Fetch project name
  const projectQuery = trpc.project.get.useQuery(
    { projectId: projectIdNum! },
    {
      enabled: !!projectIdNum,
      staleTime: 5 * 60 * 1000,
    }
  )

  // Global keyboard shortcuts
  useKeyboardShortcuts(
    {
      onShowHelp: () => setShortcutsOpen(true),
      onCommandPalette: openCommandPalette,
      onCloseModal: () => setShortcutsOpen(false),
    },
    {
      activeCategories: ['global'],
      enabled: true,
    }
  )

  const projectName = projectQuery.data?.name

  return (
    <div className="min-h-screen bg-background flex flex-col">
      {/* Header */}
      <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="flex h-14 items-center px-4">
          {/* Left: Back + Sidebar Toggle + Logo */}
          <div className="flex items-center gap-2">
            <button
              onClick={() => navigate('/projects')}
              className="p-2 text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200 rounded-md hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
              title="Back to projects"
            >
              <BackIcon />
            </button>
            <button
              onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
              className="p-2 text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200 rounded-md hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors md:hidden"
              title={sidebarCollapsed ? 'Show sidebar' : 'Hide sidebar'}
            >
              {sidebarCollapsed ? <MenuIcon /> : <CloseIcon />}
            </button>
            <Link className="flex items-center space-x-2" to="/">
              <span className="font-bold text-xl">Kanbu</span>
            </Link>
            {projectName && (
              <>
                <span className="text-gray-400">/</span>
                <span className="font-medium text-gray-700 dark:text-gray-300 truncate max-w-[200px]">
                  {projectName}
                </span>
              </>
            )}
          </div>

          {/* Right: Nav + Workspace */}
          <div className="flex flex-1 items-center justify-end space-x-4">
            <nav className="hidden md:flex items-center space-x-6 text-sm font-medium">
              <Link
                to="/projects"
                className="transition-colors hover:text-foreground/80 text-foreground/60"
              >
                Projects
              </Link>
            </nav>
            <WorkspaceSelector />
          </div>
        </div>
      </header>

      {/* Main Content with Sidebar */}
      <div className="flex flex-1 overflow-hidden">
        {/* Sidebar - hidden on mobile when collapsed */}
        <div className={`hidden md:block ${sidebarCollapsed ? 'md:hidden' : ''}`}>
          {projectIdNum && (
            <ProjectSidebar
              projectId={projectIdNum}
              projectName={projectName}
              collapsed={false}
            />
          )}
        </div>

        {/* Mobile Sidebar Overlay */}
        {!sidebarCollapsed && (
          <div className="md:hidden fixed inset-0 z-40 bg-black/50" onClick={() => setSidebarCollapsed(true)}>
            <div className="w-56 h-full" onClick={(e) => e.stopPropagation()}>
              {projectIdNum && (
                <ProjectSidebar
                  projectId={projectIdNum}
                  projectName={projectName}
                  collapsed={false}
                />
              )}
            </div>
          </div>
        )}

        {/* Page Content */}
        <main className="flex-1 overflow-auto">
          {children}
        </main>
      </div>

      {/* Global Command Palette (Cmd+K) */}
      <CommandPalette />

      {/* Keyboard Shortcuts Help (?) */}
      <ShortcutsModal isOpen={shortcutsOpen} onClose={() => setShortcutsOpen(false)} />
    </div>
  )
}

export default ProjectLayout
