import { useState } from 'react'
import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardDescription,
} from '../../components/ui/card'
import { Button } from '../../components/ui/button'
import { Input } from '../../components/ui/input'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Helper Functions
// =============================================================================

function formatDateTime(date: Date | string): string {
  const d = typeof date === 'string' ? new Date(date) : date
  return d.toLocaleString('en-US', {
    month: 'short',
    day: 'numeric',
    year: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
  })
}

// =============================================================================
// Component
// =============================================================================

export function Metadata() {
  const [newKey, setNewKey] = useState('')
  const [newValue, setNewValue] = useState('')
  const [editingKey, setEditingKey] = useState<string | null>(null)
  const [editValue, setEditValue] = useState('')

  const utils = trpc.useUtils()
  const { data: metadata, isLoading } = trpc.user.getMetadata.useQuery()

  const setMetadata = trpc.user.setMetadata.useMutation({
    onSuccess: () => {
      utils.user.getMetadata.invalidate()
      setNewKey('')
      setNewValue('')
      setEditingKey(null)
      setEditValue('')
    },
  })

  const deleteMetadata = trpc.user.deleteMetadata.useMutation({
    onSuccess: () => {
      utils.user.getMetadata.invalidate()
    },
  })

  const handleAdd = () => {
    if (newKey.trim() && newValue.trim()) {
      setMetadata.mutate({ key: newKey.trim(), value: newValue.trim() })
    }
  }

  const handleEdit = (key: string) => {
    if (editValue.trim()) {
      setMetadata.mutate({ key, value: editValue.trim() })
    }
  }

  const startEditing = (key: string, value: string) => {
    setEditingKey(key)
    setEditValue(value)
  }

  if (isLoading) {
    return (
      <ProfileLayout title="Metadata" description="Custom key-value data for your profile">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading metadata...</p>
        </div>
      </ProfileLayout>
    )
  }

  return (
    <ProfileLayout title="Metadata" description="Custom key-value data for your profile">
      <div className="space-y-6">
        {/* Add New Metadata */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Add Metadata</CardTitle>
            <CardDescription>
              Store custom key-value pairs for your profile
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="flex gap-3">
              <Input
                placeholder="Key"
                value={newKey}
                onChange={(e) => setNewKey(e.target.value)}
                className="max-w-[200px]"
              />
              <Input
                placeholder="Value"
                value={newValue}
                onChange={(e) => setNewValue(e.target.value)}
                className="flex-1"
              />
              <Button
                onClick={handleAdd}
                disabled={!newKey.trim() || !newValue.trim() || setMetadata.isPending}
              >
                {setMetadata.isPending ? 'Adding...' : 'Add'}
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* Existing Metadata */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg flex items-center justify-between">
              <span>Your Metadata</span>
              <span className="text-sm font-normal text-muted-foreground">
                {metadata?.length ?? 0} items
              </span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            {!metadata || metadata.length === 0 ? (
              <p className="text-sm text-muted-foreground text-center py-8">
                No metadata stored yet
              </p>
            ) : (
              <div className="divide-y divide-gray-100 dark:divide-gray-800">
                {metadata.map((item) => (
                  <div
                    key={item.id}
                    className="py-4"
                  >
                    {editingKey === item.key ? (
                      <div className="flex gap-3 items-center">
                        <span className="font-mono text-sm bg-gray-100 dark:bg-gray-800 px-2 py-1 rounded min-w-[120px]">
                          {item.key}
                        </span>
                        <Input
                          value={editValue}
                          onChange={(e) => setEditValue(e.target.value)}
                          className="flex-1"
                          autoFocus
                        />
                        <Button
                          size="sm"
                          onClick={() => handleEdit(item.key)}
                          disabled={setMetadata.isPending}
                        >
                          Save
                        </Button>
                        <Button
                          size="sm"
                          variant="ghost"
                          onClick={() => setEditingKey(null)}
                        >
                          Cancel
                        </Button>
                      </div>
                    ) : (
                      <div className="flex items-start justify-between gap-4">
                        <div className="flex-1 min-w-0">
                          <div className="flex items-center gap-2">
                            <span className="font-mono text-sm bg-gray-100 dark:bg-gray-800 px-2 py-1 rounded">
                              {item.key}
                            </span>
                          </div>
                          <p className="mt-2 text-sm break-words">{item.value}</p>
                          <p className="mt-1 text-xs text-muted-foreground">
                            Added {formatDateTime(item.createdAt)}
                          </p>
                        </div>
                        <div className="flex gap-2">
                          <Button
                            size="sm"
                            variant="ghost"
                            onClick={() => startEditing(item.key, item.value)}
                          >
                            Edit
                          </Button>
                          <Button
                            size="sm"
                            variant="ghost"
                            className="text-red-600 hover:text-red-700 hover:bg-red-50 dark:hover:bg-red-900/20"
                            onClick={() => deleteMetadata.mutate({ key: item.key })}
                            disabled={deleteMetadata.isPending}
                          >
                            Delete
                          </Button>
                        </div>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </ProfileLayout>
  )
}

export default Metadata
