import { useState, useCallback, useRef, useEffect } from 'react'
import { Pencil, Check, X } from 'lucide-react'
import { Button } from '@/components/ui/button'

// =============================================================================
// Types
// =============================================================================

export interface TaskDescriptionProps {
  description: string
  onUpdate: (description: string) => Promise<void>
  isUpdating: boolean
}

// =============================================================================
// Component
// =============================================================================

export function TaskDescription({
  description,
  onUpdate,
  isUpdating,
}: TaskDescriptionProps) {
  const [isEditing, setIsEditing] = useState(false)
  const [editedDescription, setEditedDescription] = useState(description)
  const textareaRef = useRef<HTMLTextAreaElement>(null)

  // Reset edited description when prop changes
  useEffect(() => {
    if (!isEditing) {
      setEditedDescription(description)
    }
  }, [description, isEditing])

  // Focus and resize textarea when editing starts
  useEffect(() => {
    if (isEditing && textareaRef.current) {
      textareaRef.current.focus()
      resizeTextarea()
    }
  }, [isEditing])

  const resizeTextarea = useCallback(() => {
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto'
      textareaRef.current.style.height = `${Math.max(200, textareaRef.current.scrollHeight)}px`
    }
  }, [])

  const handleSave = useCallback(async () => {
    if (editedDescription !== description) {
      await onUpdate(editedDescription)
    }
    setIsEditing(false)
  }, [editedDescription, description, onUpdate])

  const handleCancel = useCallback(() => {
    setEditedDescription(description)
    setIsEditing(false)
  }, [description])

  const handleKeyDown = useCallback(
    (e: React.KeyboardEvent) => {
      if (e.key === 'Escape') {
        handleCancel()
      } else if (e.key === 's' && (e.ctrlKey || e.metaKey)) {
        e.preventDefault()
        handleSave()
      }
    },
    [handleCancel, handleSave]
  )

  if (isEditing) {
    return (
      <div className="space-y-3">
        <div className="flex items-center justify-between">
          <h3 className="text-sm font-medium text-gray-700 dark:text-gray-300">
            Description
          </h3>
          <div className="flex items-center gap-2">
            <Button
              size="sm"
              variant="ghost"
              onClick={handleCancel}
              disabled={isUpdating}
            >
              <X className="h-4 w-4 mr-1" />
              Cancel
            </Button>
            <Button
              size="sm"
              onClick={handleSave}
              disabled={isUpdating}
            >
              <Check className="h-4 w-4 mr-1" />
              Save
            </Button>
          </div>
        </div>
        <textarea
          ref={textareaRef}
          value={editedDescription}
          onChange={(e) => {
            setEditedDescription(e.target.value)
            resizeTextarea()
          }}
          onKeyDown={handleKeyDown}
          className="w-full min-h-[200px] p-3 text-sm font-mono bg-gray-50 dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none"
          placeholder="Add a description... (Markdown supported)"
          disabled={isUpdating}
        />
        <p className="text-xs text-gray-500 dark:text-gray-400">
          Ctrl+S to save, Escape to cancel
        </p>
      </div>
    )
  }

  return (
    <div className="space-y-3">
      <div className="flex items-center justify-between">
        <h3 className="text-sm font-medium text-gray-700 dark:text-gray-300">
          Description
        </h3>
        <Button
          size="sm"
          variant="ghost"
          onClick={() => setIsEditing(true)}
        >
          <Pencil className="h-4 w-4 mr-1" />
          Edit
        </Button>
      </div>

      {description ? (
        <div
          className="prose prose-sm dark:prose-invert max-w-none p-3 bg-gray-50 dark:bg-gray-800 rounded-lg cursor-pointer hover:bg-gray-100 dark:hover:bg-gray-750"
          onClick={() => setIsEditing(true)}
        >
          {/* Simple markdown-like rendering */}
          {description.split('\n').map((line, i) => {
            if (line.startsWith('# ')) {
              return <h1 key={i} className="text-lg font-bold">{line.slice(2)}</h1>
            }
            if (line.startsWith('## ')) {
              return <h2 key={i} className="text-base font-semibold">{line.slice(3)}</h2>
            }
            if (line.startsWith('- ')) {
              return <li key={i} className="ml-4">{line.slice(2)}</li>
            }
            if (line.startsWith('* ')) {
              return <li key={i} className="ml-4">{line.slice(2)}</li>
            }
            if (line.trim() === '') {
              return <br key={i} />
            }
            return <p key={i}>{line}</p>
          })}
        </div>
      ) : (
        <div
          className="p-4 text-sm text-gray-500 dark:text-gray-400 bg-gray-50 dark:bg-gray-800 rounded-lg cursor-pointer hover:bg-gray-100 dark:hover:bg-gray-750 border-2 border-dashed border-gray-300 dark:border-gray-600"
          onClick={() => setIsEditing(true)}
        >
          Click to add a description...
        </div>
      )}
    </div>
  )
}

export default TaskDescription
