// =============================================================================
// Types
// =============================================================================

interface TimelineTask {
  id: number
  title: string
  priority: number
  dateStarted: string | null
  dateDue: string | null
  isActive: boolean
  progress: number
  column?: { title: string } | null
}

type ZoomLevel = 'day' | 'week' | 'month'

