/*
 * PublicAccess Page
 * Version: 1.0.0
 *
 * User profile page for managing public access tokens and feeds.
 *
 * Task: USER-01 (Task 247)
 */

import { useState } from 'react'
import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardDescription,
} from '../../components/ui/card'
import { Button } from '../../components/ui/button'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Component
// =============================================================================

export function PublicAccess() {
  const [copied, setCopied] = useState<string | null>(null)

  const utils = trpc.useUtils()
  const { data: access, isLoading } = trpc.user.getPublicAccess.useQuery()

  const enableAccess = trpc.user.enablePublicAccess.useMutation({
    onSuccess: () => {
      utils.user.getPublicAccess.invalidate()
    },
  })

  const disableAccess = trpc.user.disablePublicAccess.useMutation({
    onSuccess: () => {
      utils.user.getPublicAccess.invalidate()
    },
  })

  const regenerateToken = trpc.user.regeneratePublicToken.useMutation({
    onSuccess: () => {
      utils.user.getPublicAccess.invalidate()
    },
  })

  const copyToClipboard = async (text: string, id: string) => {
    await navigator.clipboard.writeText(text)
    setCopied(id)
    setTimeout(() => setCopied(null), 2000)
  }

  if (isLoading) {
    return (
      <ProfileLayout title="Public Access" description="Manage public RSS and iCal feeds">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading...</p>
        </div>
      </ProfileLayout>
    )
  }

  const baseUrl = window.location.origin

  return (
    <ProfileLayout title="Public Access" description="Manage public RSS and iCal feeds">
      <div className="space-y-6 max-w-2xl">
        {/* Status Card */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg flex items-center gap-2">
              Public Access
              <span className={`inline-flex items-center px-2 py-0.5 rounded text-xs font-medium ${
                access?.enabled
                  ? 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400'
                  : 'bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-400'
              }`}>
                {access?.enabled ? 'Enabled' : 'Disabled'}
              </span>
            </CardTitle>
            <CardDescription>
              {access?.enabled
                ? 'Your public feeds are accessible via the URLs below.'
                : 'Enable public access to get RSS and iCal feed URLs for your tasks.'}
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            {!access?.enabled ? (
              <Button
                onClick={() => enableAccess.mutate()}
                disabled={enableAccess.isPending}
                className="w-full"
              >
                {enableAccess.isPending ? 'Enabling...' : 'Enable Public Access'}
              </Button>
            ) : (
              <div className="space-y-4">
                {/* Token Display */}
                <div className="space-y-2">
                  <p className="text-sm font-medium">Your Public Token</p>
                  <div className="flex gap-2">
                    <code className="flex-1 p-3 bg-gray-50 dark:bg-gray-800 rounded font-mono text-xs break-all">
                      {access.token}
                    </code>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => copyToClipboard(access.token!, 'token')}
                    >
                      {copied === 'token' ? 'Copied!' : 'Copy'}
                    </Button>
                  </div>
                </div>

                {/* Feed URLs */}
                <div className="space-y-3">
                  <p className="text-sm font-medium">Feed URLs</p>

                  {/* RSS Feed */}
                  <div className="space-y-1">
                    <label className="text-xs text-muted-foreground">RSS Feed (Tasks)</label>
                    <div className="flex gap-2">
                      <code className="flex-1 p-2 bg-gray-50 dark:bg-gray-800 rounded font-mono text-xs break-all">
                        {baseUrl}/api/feed/user/{access.token}
                      </code>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => copyToClipboard(`${baseUrl}/api/feed/user/${access.token}`, 'rss')}
                      >
                        {copied === 'rss' ? 'Copied!' : 'Copy'}
                      </Button>
                    </div>
                  </div>

                  {/* iCal Feed */}
                  <div className="space-y-1">
                    <label className="text-xs text-muted-foreground">iCal Feed (Calendar)</label>
                    <div className="flex gap-2">
                      <code className="flex-1 p-2 bg-gray-50 dark:bg-gray-800 rounded font-mono text-xs break-all">
                        {baseUrl}/api/ical/user/{access.token}
                      </code>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => copyToClipboard(`${baseUrl}/api/ical/user/${access.token}`, 'ical')}
                      >
                        {copied === 'ical' ? 'Copied!' : 'Copy'}
                      </Button>
                    </div>
                  </div>
                </div>

                {/* Actions */}
                <div className="flex gap-3 pt-4 border-t">
                  <Button
                    variant="outline"
                    onClick={() => regenerateToken.mutate()}
                    disabled={regenerateToken.isPending}
                    className="flex-1"
                  >
                    {regenerateToken.isPending ? 'Regenerating...' : 'Regenerate Token'}
                  </Button>
                  <Button
                    variant="outline"
                    onClick={() => disableAccess.mutate()}
                    disabled={disableAccess.isPending}
                    className="flex-1 text-red-600 hover:text-red-700 hover:bg-red-50 dark:hover:bg-red-900/20"
                  >
                    {disableAccess.isPending ? 'Disabling...' : 'Disable Access'}
                  </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Info Card */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">About Public Access</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3 text-sm text-muted-foreground">
            <p>
              <strong>RSS Feed:</strong> Subscribe to your tasks in any RSS reader to stay updated on task changes.
            </p>
            <p>
              <strong>iCal Feed:</strong> Subscribe in your calendar app (Google Calendar, Outlook, Apple Calendar) to see task due dates.
            </p>
            <p className="text-yellow-600 dark:text-yellow-400">
              <strong>Security Note:</strong> Anyone with your public token can access your task information. Regenerate your token if you suspect it has been compromised.
            </p>
          </CardContent>
        </Card>
      </div>
    </ProfileLayout>
  )
}

export default PublicAccess
