import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
} from '../../components/ui/card'
import { Button } from '../../components/ui/button'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Helper Components
// =============================================================================

function InfoRow({ label, value, className }: { label: string; value: React.ReactNode; className?: string }) {
  return (
    <div className={`flex justify-between py-2 border-b border-gray-100 dark:border-gray-800 last:border-0 ${className || ''}`}>
      <span className="text-sm text-muted-foreground">{label}</span>
      <span className="text-sm font-medium">{value}</span>
    </div>
  )
}

function StatusBadge({ active, label }: { active: boolean; label?: string }) {
  return (
    <span className={`inline-flex items-center px-2 py-0.5 rounded text-xs font-medium ${
      active
        ? 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400'
        : 'bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-400'
    }`}>
      {label || (active ? 'Active' : 'Inactive')}
    </span>
  )
}

// =============================================================================
// Component
// =============================================================================

export function ProfileSummary() {
  const utils = trpc.useUtils()
  const { data: profile, isLoading } = trpc.user.getProfile.useQuery()
  const verifyEmail = trpc.user.verifyEmail.useMutation({
    onSuccess: () => {
      utils.user.getProfile.invalidate()
    },
  })

  if (isLoading) {
    return (
      <ProfileLayout title="Profile Summary" description="Overview of your account">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading profile...</p>
        </div>
      </ProfileLayout>
    )
  }

  if (!profile) {
    return (
      <ProfileLayout title="Profile Summary" description="Overview of your account">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Profile not found</p>
        </div>
      </ProfileLayout>
    )
  }

  // Format notification filter
  const notificationFilterLabels: Record<number, string> = {
    1: 'All tasks',
    2: 'Assigned to me',
    3: 'Created by me',
    4: 'Assigned or created',
  }

  return (
    <ProfileLayout title="Profile Summary" description="Overview of your account">
      <div className="grid gap-6 md:grid-cols-2">
        {/* Basic Info */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Basic Information</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center gap-4 mb-4">
              {profile.avatarUrl ? (
                <img
                  src={profile.avatarUrl}
                  alt={profile.name}
                  className="w-16 h-16 rounded-full"
                />
              ) : (
                <div className="w-16 h-16 rounded-full bg-blue-500 flex items-center justify-center text-white text-xl font-bold">
                  {profile.name?.charAt(0)?.toUpperCase() || profile.username.charAt(0).toUpperCase()}
                </div>
              )}
              <div>
                <p className="font-semibold text-lg">{profile.name}</p>
                <p className="text-sm text-muted-foreground">@{profile.username}</p>
              </div>
            </div>
            <InfoRow label="Email" value={profile.email} />
            <InfoRow label="Username" value={`@${profile.username}`} />
            <InfoRow label="Display Name" value={profile.name} />
            <InfoRow label="Account Status" value={<StatusBadge active={profile.isActive} />} />
            <InfoRow
              label="Email Verified"
              value={
                profile.emailVerified ? (
                  <StatusBadge active={true} label="Verified" />
                ) : (
                  <div className="flex items-center gap-2">
                    <StatusBadge active={false} label="Not verified" />
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => verifyEmail.mutate()}
                      disabled={verifyEmail.isPending}
                      className="h-6 text-xs"
                    >
                      {verifyEmail.isPending ? 'Verifying...' : 'Verify'}
                    </Button>
                  </div>
                )
              }
            />
            <InfoRow label="Member Since" value={new Date(profile.createdAt).toLocaleDateString()} />
          </CardContent>
        </Card>

        {/* Security */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Security</CardTitle>
          </CardHeader>
          <CardContent>
            <InfoRow
              label="Two-Factor Auth"
              value={<StatusBadge active={profile.twofactorActivated} label={profile.twofactorActivated ? 'Enabled' : 'Disabled'} />}
            />
            <InfoRow
              label="Last Login"
              value={profile.lastLoginAt ? new Date(profile.lastLoginAt).toLocaleString() : 'Never'}
            />
            <InfoRow
              label="Failed Login Attempts"
              value={profile.failedLoginCount}
            />
            <InfoRow
              label="Account Locked"
              value={profile.lockedUntil ? `Until ${new Date(profile.lockedUntil).toLocaleString()}` : 'No'}
            />

            {/* OAuth Connections */}
            <div className="mt-4 pt-4 border-t border-gray-100 dark:border-gray-800">
              <p className="text-sm font-medium mb-2">Connected Accounts</p>
              <div className="flex gap-2">
                <span className={`inline-flex items-center px-2 py-1 rounded text-xs ${
                  profile.googleId ? 'bg-red-100 text-red-800' : 'bg-gray-100 text-gray-400'
                }`}>
                  Google {profile.googleId ? '(connected)' : ''}
                </span>
                <span className={`inline-flex items-center px-2 py-1 rounded text-xs ${
                  profile.githubId ? 'bg-gray-800 text-white' : 'bg-gray-100 text-gray-400'
                }`}>
                  GitHub {profile.githubId ? '(connected)' : ''}
                </span>
                <span className={`inline-flex items-center px-2 py-1 rounded text-xs ${
                  profile.gitlabId ? 'bg-orange-100 text-orange-800' : 'bg-gray-100 text-gray-400'
                }`}>
                  GitLab {profile.gitlabId ? '(connected)' : ''}
                </span>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Preferences */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Preferences</CardTitle>
          </CardHeader>
          <CardContent>
            <InfoRow label="Theme" value={profile.theme || 'system'} />
            <InfoRow label="Timezone" value={profile.timezone} />
            <InfoRow label="Language" value={profile.language} />
            <InfoRow
              label="Notifications"
              value={<StatusBadge active={profile.notificationsEnabled} label={profile.notificationsEnabled ? 'On' : 'Off'} />}
            />
            <InfoRow
              label="Notification Filter"
              value={notificationFilterLabels[profile.notificationFilter] || 'Unknown'}
            />
            {profile.hourlyRate && (
              <InfoRow label="Hourly Rate" value={`${profile.hourlyRate} EUR`} />
            )}
          </CardContent>
        </Card>

        {/* Public Access */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Public Access</CardTitle>
          </CardHeader>
          <CardContent>
            {profile.publicToken ? (
              <>
                <InfoRow label="Status" value={<StatusBadge active={true} label="Enabled" />} />
                <div className="mt-4 space-y-2">
                  <p className="text-sm font-medium">Feed URLs</p>
                  <div className="text-xs text-muted-foreground bg-gray-50 dark:bg-gray-800 p-2 rounded font-mono break-all">
                    RSS: /api/feed/user/{profile.publicToken.slice(0, 8)}...
                  </div>
                  <div className="text-xs text-muted-foreground bg-gray-50 dark:bg-gray-800 p-2 rounded font-mono break-all">
                    iCal: /api/ical/user/{profile.publicToken.slice(0, 8)}...
                  </div>
                </div>
              </>
            ) : (
              <div className="text-center py-4">
                <p className="text-sm text-muted-foreground">Public access is disabled.</p>
                <p className="text-xs text-muted-foreground mt-1">
                  Enable it in Public Access settings to get RSS and iCal feeds.
                </p>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Account Stats */}
        <Card className="md:col-span-2">
          <CardHeader>
            <CardTitle className="text-lg">Account Statistics</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
              <div className="p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <p className="text-2xl font-bold">{profile.workspaceCount}</p>
                <p className="text-sm text-muted-foreground">Workspaces</p>
              </div>
              <div className="p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <p className="text-2xl font-bold">{profile.emailVerified ? '1' : '0'}</p>
                <p className="text-sm text-muted-foreground">Verified Emails</p>
              </div>
              <div className="p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <p className="text-2xl font-bold">{profile.twofactorActivated ? '1' : '0'}</p>
                <p className="text-sm text-muted-foreground">2FA Methods</p>
              </div>
              <div className="p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <p className="text-2xl font-bold">
                  {[profile.googleId, profile.githubId, profile.gitlabId].filter(Boolean).length}
                </p>
                <p className="text-sm text-muted-foreground">Connected Accounts</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </ProfileLayout>
  )
}

export default ProfileSummary
