/*
 * ApiTokens Page
 * Version: 1.0.0
 *
 * User profile page for managing API tokens (personal access tokens).
 *
 * Task: USER-01 (Task 247)
 */

import { useState } from 'react'
import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardDescription,
} from '../../components/ui/card'
import { Button } from '../../components/ui/button'
import { Input } from '../../components/ui/input'
import { Label } from '../../components/ui/label'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Helper Functions
// =============================================================================

function formatDate(date: Date | string): string {
  const d = typeof date === 'string' ? new Date(date) : date
  return d.toLocaleDateString('en-US', {
    month: 'short',
    day: 'numeric',
    year: 'numeric',
  })
}

function formatDateTime(date: Date | string | null): string {
  if (!date) return 'Never'
  const d = typeof date === 'string' ? new Date(date) : date
  return d.toLocaleString('en-US', {
    month: 'short',
    day: 'numeric',
    year: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
  })
}

// =============================================================================
// Component
// =============================================================================

export function ApiTokens() {
  const [showCreate, setShowCreate] = useState(false)
  const [newKeyName, setNewKeyName] = useState('')
  const [selectedPermissions, setSelectedPermissions] = useState<string[]>([])
  const [expiresIn, setExpiresIn] = useState<string>('never')
  const [newlyCreatedKey, setNewlyCreatedKey] = useState<string | null>(null)
  const [confirmRevoke, setConfirmRevoke] = useState<number | null>(null)
  const [copied, setCopied] = useState(false)

  const utils = trpc.useUtils()
  const { data: keys, isLoading } = trpc.apiKey.list.useQuery()
  const { data: permissions } = trpc.apiKey.getPermissions.useQuery()

  const createKey = trpc.apiKey.create.useMutation({
    onSuccess: (data) => {
      setNewlyCreatedKey(data.key)
      setShowCreate(false)
      setNewKeyName('')
      setSelectedPermissions([])
      setExpiresIn('never')
      utils.apiKey.list.invalidate()
    },
  })

  const revokeKey = trpc.apiKey.revoke.useMutation({
    onSuccess: () => {
      utils.apiKey.list.invalidate()
      setConfirmRevoke(null)
    },
  })

  const handleCreate = () => {
    let expiresAt: string | undefined
    if (expiresIn !== 'never') {
      const days = parseInt(expiresIn)
      const date = new Date()
      date.setDate(date.getDate() + days)
      expiresAt = date.toISOString()
    }

    createKey.mutate({
      name: newKeyName,
      permissions: selectedPermissions as Parameters<typeof createKey.mutate>[0]['permissions'],
      expiresAt,
    })
  }

  const copyToClipboard = async (text: string) => {
    await navigator.clipboard.writeText(text)
    setCopied(true)
    setTimeout(() => setCopied(false), 2000)
  }

  const togglePermission = (perm: string) => {
    setSelectedPermissions((prev) =>
      prev.includes(perm)
        ? prev.filter((p) => p !== perm)
        : [...prev, perm]
    )
  }

  if (isLoading) {
    return (
      <ProfileLayout title="API Tokens" description="Manage your personal access tokens">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading...</p>
        </div>
      </ProfileLayout>
    )
  }

  // Show newly created key (one-time display)
  if (newlyCreatedKey) {
    return (
      <ProfileLayout title="API Tokens" description="Manage your personal access tokens">
        <Card className="max-w-2xl">
          <CardHeader>
            <CardTitle className="text-lg text-green-600 dark:text-green-400">
              API Token Created Successfully!
            </CardTitle>
            <CardDescription>
              Copy your new API token now. You won't be able to see it again!
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="p-4 bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-800 rounded-lg">
              <p className="text-sm text-yellow-800 dark:text-yellow-200 mb-3">
                Make sure to copy your API token now. For security reasons, it won't be shown again.
              </p>
              <div className="flex gap-2">
                <code className="flex-1 p-3 bg-white dark:bg-gray-900 rounded font-mono text-sm break-all border">
                  {newlyCreatedKey}
                </code>
                <Button
                  onClick={() => copyToClipboard(newlyCreatedKey)}
                  variant="outline"
                >
                  {copied ? 'Copied!' : 'Copy'}
                </Button>
              </div>
            </div>
            <Button onClick={() => setNewlyCreatedKey(null)} className="w-full">
              Done
            </Button>
          </CardContent>
        </Card>
      </ProfileLayout>
    )
  }

  return (
    <ProfileLayout title="API Tokens" description="Manage your personal access tokens">
      <div className="space-y-6 max-w-2xl">
        {/* Create New Token */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg flex items-center justify-between">
              <span>API Tokens</span>
              {!showCreate && (
                <Button size="sm" onClick={() => setShowCreate(true)}>
                  Create Token
                </Button>
              )}
            </CardTitle>
            <CardDescription>
              Create personal access tokens to authenticate API requests.
            </CardDescription>
          </CardHeader>

          {showCreate && (
            <CardContent className="space-y-4 border-t pt-4">
              <div className="space-y-2">
                <Label htmlFor="keyName">Token Name</Label>
                <Input
                  id="keyName"
                  value={newKeyName}
                  onChange={(e) => setNewKeyName(e.target.value)}
                  placeholder="e.g., CI/CD Pipeline"
                />
              </div>

              <div className="space-y-2">
                <Label>Permissions</Label>
                <div className="grid grid-cols-2 gap-2">
                  {permissions?.map((perm) => (
                    <label
                      key={perm.value}
                      className={`
                        flex items-center gap-2 p-2 rounded border cursor-pointer text-sm
                        ${selectedPermissions.includes(perm.value)
                          ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                          : 'border-gray-200 dark:border-gray-700'}
                      `}
                    >
                      <input
                        type="checkbox"
                        checked={selectedPermissions.includes(perm.value)}
                        onChange={() => togglePermission(perm.value)}
                      />
                      <span>{perm.label}</span>
                    </label>
                  ))}
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="expires">Expiration</Label>
                <select
                  id="expires"
                  value={expiresIn}
                  onChange={(e) => setExpiresIn(e.target.value)}
                  className="w-full h-10 px-3 rounded-md border border-input bg-background text-sm"
                >
                  <option value="never">No expiration</option>
                  <option value="7">7 days</option>
                  <option value="30">30 days</option>
                  <option value="90">90 days</option>
                  <option value="365">1 year</option>
                </select>
              </div>

              <div className="flex gap-3 pt-2">
                <Button
                  variant="outline"
                  onClick={() => {
                    setShowCreate(false)
                    setNewKeyName('')
                    setSelectedPermissions([])
                  }}
                  className="flex-1"
                >
                  Cancel
                </Button>
                <Button
                  onClick={handleCreate}
                  disabled={!newKeyName.trim() || createKey.isPending}
                  className="flex-1"
                >
                  {createKey.isPending ? 'Creating...' : 'Create Token'}
                </Button>
              </div>
            </CardContent>
          )}
        </Card>

        {/* Existing Tokens */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg flex items-center justify-between">
              <span>Your Tokens</span>
              <span className="text-sm font-normal text-muted-foreground">
                {keys?.length ?? 0} tokens
              </span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            {!keys || keys.length === 0 ? (
              <p className="text-sm text-muted-foreground text-center py-8">
                No API tokens created yet
              </p>
            ) : (
              <div className="divide-y divide-gray-100 dark:divide-gray-800">
                {keys.map((key) => (
                  <div key={key.id} className="py-4">
                    <div className="flex items-start justify-between gap-4">
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center gap-2">
                          <h4 className="font-medium">{key.name}</h4>
                          {!key.isActive && (
                            <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-400">
                              Inactive
                            </span>
                          )}
                          {key.isExpired && (
                            <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-400">
                              Expired
                            </span>
                          )}
                        </div>
                        <p className="text-sm text-muted-foreground mt-1">
                          <code className="bg-gray-100 dark:bg-gray-800 px-1 rounded">
                            {key.keyPrefix}...
                          </code>
                          {'  '}
                          Created {formatDate(key.createdAt)}
                        </p>
                        <p className="text-xs text-muted-foreground mt-1">
                          Last used: {formatDateTime(key.lastUsedAt)}
                          {key.expiresAt && (
                            <>
                              {'  '}
                              Expires: {formatDate(key.expiresAt)}
                            </>
                          )}
                        </p>
                        {key.permissions.length > 0 && (
                          <div className="flex flex-wrap gap-1 mt-2">
                            {key.permissions.map((perm) => (
                              <span
                                key={perm}
                                className="inline-flex items-center px-2 py-0.5 rounded text-xs bg-blue-100 text-blue-800 dark:bg-blue-900/30 dark:text-blue-400"
                              >
                                {perm}
                              </span>
                            ))}
                          </div>
                        )}
                      </div>
                      <div>
                        {confirmRevoke === key.id ? (
                          <div className="flex gap-2">
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => setConfirmRevoke(null)}
                            >
                              Cancel
                            </Button>
                            <Button
                              size="sm"
                              onClick={() => revokeKey.mutate({ keyId: key.id })}
                              disabled={revokeKey.isPending}
                              className="bg-red-600 hover:bg-red-700 text-white"
                            >
                              Confirm
                            </Button>
                          </div>
                        ) : (
                          <Button
                            size="sm"
                            variant="ghost"
                            onClick={() => setConfirmRevoke(key.id)}
                            className="text-red-600 hover:text-red-700 hover:bg-red-50 dark:hover:bg-red-900/20"
                          >
                            Revoke
                          </Button>
                        )}
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Info Card */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">About API Tokens</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3 text-sm text-muted-foreground">
            <p>
              <strong>Authentication:</strong> Use tokens in the Authorization header: <code className="bg-gray-100 dark:bg-gray-800 px-1 rounded">Authorization: Bearer kb_...</code>
            </p>
            <p>
              <strong>Permissions:</strong> Each token can have specific permissions to limit access.
            </p>
            <p className="text-yellow-600 dark:text-yellow-400">
              <strong>Security:</strong> Treat your API tokens like passwords. Never share them or commit them to version control.
            </p>
          </CardContent>
        </Card>
      </div>
    </ProfileLayout>
  )
}

export default ApiTokens
