/*
 * HourlyRate Page
 * Version: 1.0.0
 *
 * User profile page for setting hourly rate for cost calculations.
 *
 * Task: USER-01 (Task 247)
 */

import { useState, useEffect } from 'react'
import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardDescription,
} from '../../components/ui/card'
import { Button } from '../../components/ui/button'
import { Input } from '../../components/ui/input'
import { Label } from '../../components/ui/label'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Component
// =============================================================================

export function HourlyRate() {
  const [rate, setRate] = useState<string>('')
  const [hasChanges, setHasChanges] = useState(false)

  const utils = trpc.useUtils()
  const { data, isLoading } = trpc.user.getHourlyRate.useQuery()

  const updateRate = trpc.user.updateHourlyRate.useMutation({
    onSuccess: () => {
      utils.user.getHourlyRate.invalidate()
      setHasChanges(false)
    },
  })

  // Initialize form with current rate
  useEffect(() => {
    if (data?.hourlyRate !== undefined) {
      setRate(data.hourlyRate !== null ? String(data.hourlyRate) : '')
    }
  }, [data])

  // Track changes
  useEffect(() => {
    if (data !== undefined) {
      const currentRate = data.hourlyRate !== null ? String(data.hourlyRate) : ''
      setHasChanges(rate !== currentRate)
    }
  }, [rate, data])

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    const numericRate = rate.trim() === '' ? null : parseFloat(rate)
    updateRate.mutate({ hourlyRate: numericRate })
  }

  const handleRateChange = (value: string) => {
    // Only allow valid decimal numbers
    if (value === '' || /^\d*\.?\d{0,2}$/.test(value)) {
      setRate(value)
    }
  }

  if (isLoading) {
    return (
      <ProfileLayout title="Hourly Rate" description="Set your hourly rate for cost calculations">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading...</p>
        </div>
      </ProfileLayout>
    )
  }

  return (
    <ProfileLayout title="Hourly Rate" description="Set your hourly rate for cost calculations">
      <div className="space-y-6 max-w-xl">
        {/* Rate Setting */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Your Hourly Rate</CardTitle>
            <CardDescription>
              This rate is used to calculate costs based on time tracked on tasks.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="hourlyRate">Hourly Rate (EUR)</Label>
                <div className="relative">
                  <span className="absolute left-3 top-1/2 -translate-y-1/2 text-muted-foreground">
                    
                  </span>
                  <Input
                    id="hourlyRate"
                    type="text"
                    inputMode="decimal"
                    value={rate}
                    onChange={(e) => handleRateChange(e.target.value)}
                    placeholder="0.00"
                    className="pl-8 text-right font-mono"
                  />
                </div>
                <p className="text-xs text-muted-foreground">
                  Leave empty if you don't want to track costs
                </p>
              </div>

              <div className="flex gap-3">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => {
                    const currentRate = data?.hourlyRate !== null ? String(data?.hourlyRate ?? '') : ''
                    setRate(currentRate)
                  }}
                  disabled={!hasChanges}
                  className="flex-1"
                >
                  Reset
                </Button>
                <Button
                  type="submit"
                  disabled={!hasChanges || updateRate.isPending}
                  className="flex-1"
                >
                  {updateRate.isPending ? 'Saving...' : 'Save'}
                </Button>
              </div>

              {updateRate.isSuccess && (
                <p className="text-sm text-green-600 dark:text-green-400 text-center">
                  Hourly rate updated successfully!
                </p>
              )}

              {updateRate.isError && (
                <p className="text-sm text-red-600 dark:text-red-400 text-center">
                  Failed to update hourly rate. Please try again.
                </p>
              )}
            </form>
          </CardContent>
        </Card>

        {/* Cost Calculation Example */}
        {rate && parseFloat(rate) > 0 && (
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Cost Calculation Preview</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2 text-sm">
                <div className="flex justify-between">
                  <span className="text-muted-foreground">1 hour of work:</span>
                  <span className="font-mono"> {parseFloat(rate).toFixed(2)}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-muted-foreground">8 hours (1 day):</span>
                  <span className="font-mono"> {(parseFloat(rate) * 8).toFixed(2)}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-muted-foreground">40 hours (1 week):</span>
                  <span className="font-mono"> {(parseFloat(rate) * 40).toFixed(2)}</span>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Info Card */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">About Hourly Rates</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3 text-sm text-muted-foreground">
            <p>
              <strong>Time Tracking:</strong> Your hourly rate is multiplied by time spent on tasks to calculate costs.
            </p>
            <p>
              <strong>Budget Reports:</strong> Project budgets and reports use individual hourly rates for accurate cost estimation.
            </p>
            <p>
              <strong>Privacy:</strong> Your hourly rate is only visible to you and workspace administrators.
            </p>
          </CardContent>
        </Card>
      </div>
    </ProfileLayout>
  )
}

export default HourlyRate
