import { useState } from 'react'
import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardDescription,
} from '../../components/ui/card'
import { Button } from '../../components/ui/button'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Helper Functions
// =============================================================================

function formatDateTime(date: Date | string): string {
  const d = typeof date === 'string' ? new Date(date) : date
  return d.toLocaleString('en-US', {
    month: 'short',
    day: 'numeric',
    year: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
  })
}

function getTimeRemaining(expiresAt: Date | string): string {
  const expires = typeof expiresAt === 'string' ? new Date(expiresAt) : expiresAt
  const now = new Date()
  const diff = expires.getTime() - now.getTime()

  if (diff <= 0) return 'Expired'

  const days = Math.floor(diff / (1000 * 60 * 60 * 24))
  const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))

  if (days > 0) return `${days}d ${hours}h remaining`
  if (hours > 0) return `${hours}h remaining`
  return 'Less than 1h remaining'
}

function getBrowserName(userAgent: string | null): string {
  if (!userAgent) return 'Unknown Browser'
  const ua = userAgent.toLowerCase()
  if (ua.includes('firefox')) return 'Firefox'
  if (ua.includes('edg')) return 'Edge'
  if (ua.includes('chrome')) return 'Chrome'
  if (ua.includes('safari')) return 'Safari'
  if (ua.includes('opera')) return 'Opera'
  return 'Unknown Browser'
}

function getOSName(userAgent: string | null): string {
  if (!userAgent) return 'Unknown OS'
  const ua = userAgent.toLowerCase()
  if (ua.includes('windows')) return 'Windows'
  if (ua.includes('mac')) return 'macOS'
  if (ua.includes('linux')) return 'Linux'
  if (ua.includes('android')) return 'Android'
  if (ua.includes('iphone') || ua.includes('ipad')) return 'iOS'
  return 'Unknown OS'
}

// =============================================================================
// Component
// =============================================================================

export function Sessions() {
  const [revoking, setRevoking] = useState<string | number | null>(null)

  const utils = trpc.useUtils()
  const { data: sessions, isLoading: loadingSessions } = trpc.user.getSessions.useQuery()
  const { data: tokens, isLoading: loadingTokens } = trpc.user.getRememberTokens.useQuery()

  const revokeSession = trpc.user.revokeSession.useMutation({
    onSuccess: () => {
      utils.user.getSessions.invalidate()
      setRevoking(null)
    },
  })

  const revokeToken = trpc.user.revokeRememberToken.useMutation({
    onSuccess: () => {
      utils.user.getRememberTokens.invalidate()
      setRevoking(null)
    },
  })

  const revokeAllSessions = trpc.user.revokeAllSessions.useMutation({
    onSuccess: () => {
      utils.user.getSessions.invalidate()
    },
  })

  const isLoading = loadingSessions || loadingTokens

  if (isLoading) {
    return (
      <ProfileLayout title="Persistent Connections" description="Manage your active sessions">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading sessions...</p>
        </div>
      </ProfileLayout>
    )
  }

  return (
    <ProfileLayout title="Persistent Connections" description="Manage your active sessions">
      <div className="space-y-6">
        {/* Active Sessions */}
        <Card>
          <CardHeader>
            <div className="flex items-center justify-between">
              <div>
                <CardTitle className="text-lg">Active Sessions</CardTitle>
                <CardDescription>
                  Sessions allow you to stay logged in across browser restarts
                </CardDescription>
              </div>
              {sessions && sessions.length > 0 && (
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => revokeAllSessions.mutate()}
                  disabled={revokeAllSessions.isPending}
                >
                  {revokeAllSessions.isPending ? 'Revoking...' : 'Revoke All'}
                </Button>
              )}
            </div>
          </CardHeader>
          <CardContent>
            {!sessions || sessions.length === 0 ? (
              <p className="text-sm text-muted-foreground text-center py-4">
                No active sessions
              </p>
            ) : (
              <div className="divide-y divide-gray-100 dark:divide-gray-800">
                {sessions.map((session) => (
                  <div
                    key={session.id}
                    className="py-4 flex items-center justify-between"
                  >
                    <div>
                      <div className="flex items-center gap-2">
                        <span className="font-medium">
                          {getBrowserName(session.userAgent)}
                        </span>
                        <span className="text-muted-foreground">on</span>
                        <span className="font-medium">
                          {getOSName(session.userAgent)}
                        </span>
                      </div>
                      <div className="mt-1 flex items-center gap-3 text-sm text-muted-foreground">
                        <span>{formatDateTime(session.createdAt)}</span>
                        <span></span>
                        <span className="font-mono text-xs">{session.ipAddress || 'Unknown IP'}</span>
                        <span></span>
                        <span className="text-green-600 dark:text-green-400">
                          {getTimeRemaining(session.expiresAt)}
                        </span>
                      </div>
                    </div>
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => {
                        setRevoking(session.id)
                        revokeSession.mutate({ sessionId: session.id })
                      }}
                      disabled={revoking === session.id}
                    >
                      {revoking === session.id ? 'Revoking...' : 'Revoke'}
                    </Button>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Remember Tokens */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Remember Me Tokens</CardTitle>
            <CardDescription>
              Tokens for "Remember Me" functionality on login
            </CardDescription>
          </CardHeader>
          <CardContent>
            {!tokens || tokens.length === 0 ? (
              <p className="text-sm text-muted-foreground text-center py-4">
                No remember tokens active
              </p>
            ) : (
              <div className="divide-y divide-gray-100 dark:divide-gray-800">
                {tokens.map((token) => (
                  <div
                    key={token.id}
                    className="py-4 flex items-center justify-between"
                  >
                    <div>
                      <div className="font-mono text-sm">{token.tokenPreview}</div>
                      <div className="mt-1 flex items-center gap-3 text-sm text-muted-foreground">
                        <span>Created {formatDateTime(token.createdAt)}</span>
                        <span></span>
                        <span className="text-green-600 dark:text-green-400">
                          {getTimeRemaining(token.expiresAt)}
                        </span>
                      </div>
                    </div>
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => {
                        setRevoking(token.id)
                        revokeToken.mutate({ tokenId: token.id })
                      }}
                      disabled={revoking === token.id}
                    >
                      {revoking === token.id ? 'Revoking...' : 'Revoke'}
                    </Button>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </ProfileLayout>
  )
}

export default Sessions
