/*
 * TwoFactorAuth Page
 * Version: 1.0.0
 *
 * User profile page for managing two-factor authentication.
 *
 * Task: USER-01 (Task 247)
 */

import { useState } from 'react'
import { QRCodeSVG } from 'qrcode.react'
import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardDescription,
} from '../../components/ui/card'
import { Button } from '../../components/ui/button'
import { Input } from '../../components/ui/input'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Component
// =============================================================================

export function TwoFactorAuth() {
  const [setupData, setSetupData] = useState<{ secret: string; qrCodeUri: string } | null>(null)
  const [verificationCode, setVerificationCode] = useState('')
  const [backupCodes, setBackupCodes] = useState<string[] | null>(null)
  const [disablePassword, setDisablePassword] = useState('')
  const [showDisable, setShowDisable] = useState(false)

  const utils = trpc.useUtils()
  const { data: status, isLoading } = trpc.user.get2FAStatus.useQuery()

  const setup2FA = trpc.user.setup2FA.useMutation({
    onSuccess: (data) => {
      setSetupData(data)
    },
  })

  const verify2FA = trpc.user.verify2FA.useMutation({
    onSuccess: (data) => {
      setBackupCodes(data.backupCodes)
      setSetupData(null)
      setVerificationCode('')
      utils.user.get2FAStatus.invalidate()
    },
  })

  const disable2FA = trpc.user.disable2FA.useMutation({
    onSuccess: () => {
      setShowDisable(false)
      setDisablePassword('')
      utils.user.get2FAStatus.invalidate()
    },
  })

  const handleVerify = () => {
    if (verificationCode.length === 6) {
      verify2FA.mutate({ token: verificationCode })
    }
  }

  const handleDisable = () => {
    if (disablePassword) {
      disable2FA.mutate({ password: disablePassword })
    }
  }

  if (isLoading) {
    return (
      <ProfileLayout title="Two-Factor Authentication" description="Secure your account with 2FA">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading...</p>
        </div>
      </ProfileLayout>
    )
  }

  // Show backup codes after successful setup
  if (backupCodes) {
    return (
      <ProfileLayout title="Two-Factor Authentication" description="Secure your account with 2FA">
        <Card className="max-w-xl">
          <CardHeader>
            <CardTitle className="text-lg text-green-600 dark:text-green-400">
              2FA Enabled Successfully!
            </CardTitle>
            <CardDescription>
              Save these backup codes in a safe place. You can use them to access your account if you lose your authenticator device.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="bg-gray-50 dark:bg-gray-800 p-4 rounded-lg">
              <div className="grid grid-cols-2 gap-2">
                {backupCodes.map((code, index) => (
                  <code key={index} className="font-mono text-sm p-2 bg-white dark:bg-gray-900 rounded border">
                    {code}
                  </code>
                ))}
              </div>
            </div>
            <p className="text-sm text-muted-foreground">
              Each code can only be used once. Keep them safe!
            </p>
            <Button onClick={() => setBackupCodes(null)} className="w-full">
              Done
            </Button>
          </CardContent>
        </Card>
      </ProfileLayout>
    )
  }

  // Show setup flow
  if (setupData) {
    return (
      <ProfileLayout title="Two-Factor Authentication" description="Secure your account with 2FA">
        <Card className="max-w-xl">
          <CardHeader>
            <CardTitle className="text-lg">Setup Two-Factor Authentication</CardTitle>
            <CardDescription>
              Scan the QR code with your authenticator app (Google Authenticator, Authy, etc.)
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            {/* QR Code */}
            <div className="flex justify-center p-4 bg-white rounded-lg">
              <QRCodeSVG value={setupData.qrCodeUri} size={200} />
            </div>

            {/* Manual Entry */}
            <div className="space-y-2">
              <p className="text-sm font-medium">Can't scan? Enter this code manually:</p>
              <code className="block p-3 bg-gray-50 dark:bg-gray-800 rounded font-mono text-sm break-all">
                {setupData.secret}
              </code>
            </div>

            {/* Verification */}
            <div className="space-y-2">
              <p className="text-sm font-medium">Enter the 6-digit code from your app:</p>
              <div className="flex gap-3">
                <Input
                  value={verificationCode}
                  onChange={(e) => setVerificationCode(e.target.value.replace(/\D/g, '').slice(0, 6))}
                  placeholder="000000"
                  className="font-mono text-center text-lg tracking-widest"
                  maxLength={6}
                />
                <Button
                  onClick={handleVerify}
                  disabled={verificationCode.length !== 6 || verify2FA.isPending}
                >
                  {verify2FA.isPending ? 'Verifying...' : 'Verify'}
                </Button>
              </div>
              {verify2FA.isError && (
                <p className="text-sm text-red-600 dark:text-red-400">
                  {verify2FA.error.message}
                </p>
              )}
            </div>

            <Button
              variant="outline"
              onClick={() => setSetupData(null)}
              className="w-full"
            >
              Cancel
            </Button>
          </CardContent>
        </Card>
      </ProfileLayout>
    )
  }

  // Main view - enabled or disabled state
  return (
    <ProfileLayout title="Two-Factor Authentication" description="Secure your account with 2FA">
      <Card className="max-w-xl">
        <CardHeader>
          <CardTitle className="text-lg flex items-center gap-2">
            Two-Factor Authentication
            <span className={`inline-flex items-center px-2 py-0.5 rounded text-xs font-medium ${
              status?.enabled
                ? 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400'
                : 'bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-400'
            }`}>
              {status?.enabled ? 'Enabled' : 'Disabled'}
            </span>
          </CardTitle>
          <CardDescription>
            {status?.enabled
              ? 'Your account is protected with two-factor authentication.'
              : 'Add an extra layer of security to your account by enabling two-factor authentication.'}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {status?.enabled ? (
            <>
              {!showDisable ? (
                <Button
                  variant="outline"
                  onClick={() => setShowDisable(true)}
                  className="w-full text-red-600 hover:text-red-700 hover:bg-red-50 dark:hover:bg-red-900/20"
                >
                  Disable Two-Factor Authentication
                </Button>
              ) : (
                <div className="space-y-3 p-4 border border-red-200 dark:border-red-800 rounded-lg">
                  <p className="text-sm font-medium text-red-600 dark:text-red-400">
                    Enter your password to disable 2FA:
                  </p>
                  <Input
                    type="password"
                    value={disablePassword}
                    onChange={(e) => setDisablePassword(e.target.value)}
                    placeholder="Your password"
                  />
                  <div className="flex gap-2">
                    <Button
                      variant="outline"
                      onClick={() => {
                        setShowDisable(false)
                        setDisablePassword('')
                      }}
                      className="flex-1"
                    >
                      Cancel
                    </Button>
                    <Button
                      onClick={handleDisable}
                      disabled={!disablePassword || disable2FA.isPending}
                      className="flex-1 bg-red-600 hover:bg-red-700"
                    >
                      {disable2FA.isPending ? 'Disabling...' : 'Disable 2FA'}
                    </Button>
                  </div>
                  {disable2FA.isError && (
                    <p className="text-sm text-red-600 dark:text-red-400">
                      {disable2FA.error.message}
                    </p>
                  )}
                </div>
              )}
            </>
          ) : (
            <Button
              onClick={() => setup2FA.mutate()}
              disabled={setup2FA.isPending}
              className="w-full"
            >
              {setup2FA.isPending ? 'Setting up...' : 'Enable Two-Factor Authentication'}
            </Button>
          )}
        </CardContent>
      </Card>
    </ProfileLayout>
  )
}

export default TwoFactorAuth
