import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
} from '../../components/ui/card'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Helper Functions
// =============================================================================

function formatHours(hours: number): string {
  if (hours === 0) return '0h'
  if (hours < 1) {
    const minutes = Math.round(hours * 60)
    return `${minutes}m`
  }
  const h = Math.floor(hours)
  const m = Math.round((hours - h) * 60)
  return m > 0 ? `${h}h ${m}m` : `${h}h`
}

function formatDate(date: Date | string): string {
  const d = typeof date === 'string' ? new Date(date) : date
  return d.toLocaleDateString('en-US', {
    month: 'short',
    day: 'numeric',
    year: 'numeric',
  })
}

// =============================================================================
// Component
// =============================================================================

export function TimeTracking() {
  const { data, isLoading } = trpc.user.getTimeTracking.useQuery({})

  if (isLoading) {
    return (
      <ProfileLayout title="Time Tracking" description="Overview of your logged hours">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading time tracking data...</p>
        </div>
      </ProfileLayout>
    )
  }

  if (!data) {
    return (
      <ProfileLayout title="Time Tracking" description="Overview of your logged hours">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">No time tracking data found</p>
        </div>
      </ProfileLayout>
    )
  }

  const efficiency = data.totalEstimated > 0
    ? Math.round((data.totalSpent / data.totalEstimated) * 100)
    : 0

  return (
    <ProfileLayout title="Time Tracking" description="Overview of your logged hours">
      <div className="space-y-6">
        {/* Summary Stats */}
        <div className="grid gap-4 md:grid-cols-3">
          <Card>
            <CardContent className="pt-6">
              <div className="text-center">
                <p className="text-3xl font-bold text-blue-600 dark:text-blue-400">
                  {formatHours(data.totalSpent)}
                </p>
                <p className="text-sm text-muted-foreground mt-1">Total Time Spent</p>
              </div>
            </CardContent>
          </Card>
          <Card>
            <CardContent className="pt-6">
              <div className="text-center">
                <p className="text-3xl font-bold text-gray-600 dark:text-gray-400">
                  {formatHours(data.totalEstimated)}
                </p>
                <p className="text-sm text-muted-foreground mt-1">Total Estimated</p>
              </div>
            </CardContent>
          </Card>
          <Card>
            <CardContent className="pt-6">
              <div className="text-center">
                <p className={`text-3xl font-bold ${
                  efficiency <= 100
                    ? 'text-green-600 dark:text-green-400'
                    : 'text-orange-600 dark:text-orange-400'
                }`}>
                  {efficiency}%
                </p>
                <p className="text-sm text-muted-foreground mt-1">Efficiency</p>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Time by Project */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Time by Project</CardTitle>
          </CardHeader>
          <CardContent>
            {data.byProject.length === 0 ? (
              <p className="text-sm text-muted-foreground text-center py-4">
                No time logged yet
              </p>
            ) : (
              <div className="space-y-4">
                {data.byProject.map((project) => {
                  const percentage = data.totalSpent > 0
                    ? Math.round((project.timeSpent / data.totalSpent) * 100)
                    : 0
                  return (
                    <div key={project.projectId}>
                      <div className="flex justify-between items-center mb-1">
                        <span className="text-sm font-medium">{project.projectName}</span>
                        <span className="text-sm text-muted-foreground">
                          {formatHours(project.timeSpent)} / {formatHours(project.timeEstimated)}
                        </span>
                      </div>
                      <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                        <div
                          className="bg-blue-600 dark:bg-blue-500 h-2 rounded-full transition-all"
                          style={{ width: `${percentage}%` }}
                        />
                      </div>
                    </div>
                  )
                })}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Recent Time Entries */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Recent Time Entries</CardTitle>
          </CardHeader>
          <CardContent>
            {data.recentEntries.length === 0 ? (
              <p className="text-sm text-muted-foreground text-center py-4">
                No recent time entries
              </p>
            ) : (
              <div className="divide-y divide-gray-100 dark:divide-gray-800">
                {data.recentEntries.map((entry) => (
                  <div
                    key={`${entry.type}-${entry.id}`}
                    className="py-3 flex items-center justify-between"
                  >
                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-medium truncate">
                        {entry.type === 'subtask' ? (
                          <>
                            <span className="text-muted-foreground">{entry.taskTitle}  </span>
                            {entry.title}
                          </>
                        ) : (
                          entry.title
                        )}
                      </p>
                      <p className="text-xs text-muted-foreground">
                        {entry.projectName}  {formatDate(entry.updatedAt)}
                      </p>
                    </div>
                    <div className="flex items-center gap-3 ml-4">
                      <span className={`inline-flex items-center px-2 py-0.5 rounded text-xs font-medium ${
                        entry.isActive
                          ? 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/30 dark:text-yellow-400'
                          : 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400'
                      }`}>
                        {entry.isActive ? 'In Progress' : 'Done'}
                      </span>
                      <span className="text-sm font-medium whitespace-nowrap">
                        {formatHours(entry.timeSpent)}
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </ProfileLayout>
  )
}

export default TimeTracking
