/*
 * NotificationPreferences Page
 * Version: 1.0.0
 *
 * User profile page for managing notification settings.
 *
 * Task: USER-01 (Task 247)
 */

import { ProfileLayout } from '../../components/profile/ProfileLayout'
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardDescription,
} from '../../components/ui/card'
import { trpc } from '../../lib/trpc'

// =============================================================================
// Helper Components
// =============================================================================

function Toggle({
  checked,
  onChange,
  disabled,
}: {
  checked: boolean
  onChange: (checked: boolean) => void
  disabled?: boolean
}) {
  return (
    <button
      type="button"
      role="switch"
      aria-checked={checked}
      onClick={() => onChange(!checked)}
      disabled={disabled}
      className={`
        relative inline-flex h-6 w-11 shrink-0 cursor-pointer rounded-full border-2 border-transparent
        transition-colors duration-200 ease-in-out focus:outline-none focus:ring-2 focus:ring-offset-2
        ${checked ? 'bg-blue-600' : 'bg-gray-200 dark:bg-gray-700'}
        ${disabled ? 'opacity-50 cursor-not-allowed' : ''}
      `}
    >
      <span
        className={`
          pointer-events-none inline-block h-5 w-5 transform rounded-full bg-white shadow ring-0
          transition duration-200 ease-in-out
          ${checked ? 'translate-x-5' : 'translate-x-0'}
        `}
      />
    </button>
  )
}

// =============================================================================
// Component
// =============================================================================

export function NotificationPreferences() {
  const utils = trpc.useUtils()
  const { data: settings, isLoading } = trpc.notification.getSettings.useQuery()

  const updateSettings = trpc.notification.updateSettings.useMutation({
    onSuccess: () => {
      utils.notification.getSettings.invalidate()
    },
  })

  const updateTypeSetting = trpc.notification.updateTypeSetting.useMutation({
    onSuccess: () => {
      utils.notification.getSettings.invalidate()
    },
  })

  if (isLoading) {
    return (
      <ProfileLayout title="Notifications" description="Manage your notification preferences">
        <div className="flex items-center justify-center py-12">
          <p className="text-muted-foreground">Loading...</p>
        </div>
      </ProfileLayout>
    )
  }

  const filterOptions = [
    { value: 1, label: 'All tasks', description: 'Receive notifications for all tasks in your projects' },
    { value: 2, label: 'Assigned to me', description: 'Only tasks assigned to you' },
    { value: 3, label: 'Created by me', description: 'Only tasks you created' },
    { value: 4, label: 'Assigned or created by me', description: 'Tasks you created or are assigned to (recommended)' },
  ]

  const typeLabels: Record<string, { label: string; description: string }> = {
    email: { label: 'Email', description: 'Receive email notifications for important updates' },
    web: { label: 'Web', description: 'In-app notifications in the notification center' },
    push: { label: 'Push', description: 'Browser push notifications (requires permission)' },
  }

  return (
    <ProfileLayout title="Notifications" description="Manage your notification preferences">
      <div className="space-y-6 max-w-2xl">
        {/* Master Toggle */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg flex items-center justify-between">
              <span>Notifications</span>
              <Toggle
                checked={settings?.enabled ?? true}
                onChange={(checked) => updateSettings.mutate({ notificationsEnabled: checked })}
                disabled={updateSettings.isPending}
              />
            </CardTitle>
            <CardDescription>
              {settings?.enabled
                ? 'You will receive notifications based on your preferences below.'
                : 'All notifications are currently disabled.'}
            </CardDescription>
          </CardHeader>
        </Card>

        {/* Filter Settings */}
        <Card className={!settings?.enabled ? 'opacity-50 pointer-events-none' : ''}>
          <CardHeader>
            <CardTitle className="text-lg">Task Filter</CardTitle>
            <CardDescription>
              Choose which tasks should trigger notifications
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-3">
            {filterOptions.map((option) => (
              <label
                key={option.value}
                className={`
                  flex items-start gap-3 p-3 rounded-lg border cursor-pointer transition-colors
                  ${settings?.filter === option.value
                    ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                    : 'border-gray-200 dark:border-gray-700 hover:border-gray-300 dark:hover:border-gray-600'}
                `}
              >
                <input
                  type="radio"
                  name="notificationFilter"
                  value={option.value}
                  checked={settings?.filter === option.value}
                  onChange={() => updateSettings.mutate({ notificationFilter: option.value })}
                  className="mt-1"
                />
                <div>
                  <p className="font-medium">{option.label}</p>
                  <p className="text-sm text-muted-foreground">{option.description}</p>
                </div>
              </label>
            ))}
          </CardContent>
        </Card>

        {/* Delivery Methods */}
        <Card className={!settings?.enabled ? 'opacity-50 pointer-events-none' : ''}>
          <CardHeader>
            <CardTitle className="text-lg">Delivery Methods</CardTitle>
            <CardDescription>
              Choose how you want to receive notifications
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            {settings?.types.map((typeSetting) => {
              const meta = typeLabels[typeSetting.type] ?? {
                label: typeSetting.type,
                description: '',
              }
              return (
                <div
                  key={typeSetting.type}
                  className="flex items-center justify-between py-3 border-b last:border-0 dark:border-gray-700"
                >
                  <div>
                    <p className="font-medium">{meta.label}</p>
                    <p className="text-sm text-muted-foreground">{meta.description}</p>
                  </div>
                  <Toggle
                    checked={typeSetting.enabled}
                    onChange={(checked) =>
                      updateTypeSetting.mutate({
                        notificationType: typeSetting.type as 'email' | 'web' | 'push',
                        isEnabled: checked,
                      })
                    }
                    disabled={updateTypeSetting.isPending}
                  />
                </div>
              )
            })}
          </CardContent>
        </Card>

        {/* Info Card */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">About Notifications</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3 text-sm text-muted-foreground">
            <p>
              <strong>Task events:</strong> Task assignments, due date changes, comments, and status updates.
            </p>
            <p>
              <strong>Project events:</strong> Project invitations, member changes, and milestone updates.
            </p>
            <p>
              <strong>System events:</strong> Account security alerts and important announcements.
            </p>
          </CardContent>
        </Card>
      </div>
    </ProfileLayout>
  )
}

export default NotificationPreferences
